import React from "react";
import { graphql } from "gatsby";

import Excerpt from "../components/Excerpt";
import Layout from "../components/layout";

export default ({ data }) => {
	return (
		<Layout>
			<div>
				<h1>
					{data.allMarkdownRemark.totalCount} articles in the archive
				</h1>
				{ data.allMarkdownRemark.edges.map(({ node }) => ( <Excerpt key={ node.id } data={ node } /> )) }
			</div>
		</Layout>
	)
}

export const query = graphql`
query{
	allMarkdownRemark(
		filter: { 
			frontmatter:{
				date: {ne: null}
				published: {eq: true}
			}
		},
		sort: {
			order: DESC,
			fields: frontmatter___date
		}
	) {
		totalCount
		edges {
			node {
				id
				frontmatter { 
					title
					date
					category
					description
				}
				fields {
					slug
				}
				excerpt(format: HTML)
			}
		}
	}
}`
